import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
var token =""
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: "",
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: "WAITING",
					text: '待支付',
					loadingType: 'more',
					orderList: []
				},
				{
					state: "PAID",
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: "RECEIPT",
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: "OVER",
					text: '已完成',
					loadingType: 'more',
					orderList: []
				}
			],
		};
	},
	
	onLoad(options){
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		token = uni.getStorageSync("token")
		this.tabCurrentIndex = +options.state;




		if(options.state == 0){
			this.loadData()
		}

		
	},
	 
	methods: {
		//获取订单列表
		loadData(){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			var _this =this
			var dataAll=
			{
				
			}
			
			if(state==="")
			{//全部
				 dataAll=
				{
					pageNum:"0",
					pageSize:"100"
				}
			}else
			{//非全部
				dataAll=
				{
					orderStatus:state,
					pageNum:"0",
					pageSize:"100"
				}
			}
			uni.request({
			    url:this.$url+'/order/search' ,//服务器端地址
			    data: dataAll,
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.navList[index].orderList=res.data.data.content
						console.log(_this.navList)
			        }
			    }
			   
			});
		}, 

		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		
		//删除订单
		deleteOrder(id){
			var _this =this
			uni.showModal({
				title:"提示",
				content:"确定要删除订单吗？",
				confirmText:"确定",
				confirmColor:"#333233",
				cancelText:"取消",
				cancelColor:"#909399",
				success() {
					uni.request({
					    url:_this.$url+'/order/delete/'+id ,//服务器端地址
					    data: {
							
					    },
					    method: 'POST',
					    header: {
					        'content-type': 'application/json',
							'token':token,
					    },
					    success: (res) => { 
							console.log(res.data)
					        if (res.data.code == 200) {
								_this.loadData()
								uni.hideLoading()
								_this.$api.msg(res.data.data)
					        }
					    }
					   
					});
				}
			})
			
			
		},
		//取消订单
		cancelOrder(id){
			
			var _this =this
			uni.showModal({
				title:"提示",
				content:"确定要取消订单吗？",
				confirmText:"确定",
				confirmColor:"#333233",
				cancelText:"取消",
				cancelColor:"#909399",
				success() {
					uni.request({
					    url:_this.$url+'/order/cancel/'+id ,//服务器端地址
					    data: {
							
					    },
					    method: 'POST',
					    header: {
					        'content-type': 'application/json',
							'token':token,
					    },
					    success: (res) => { 
							console.log(res.data)
					        if (res.data.code == 200) {
								_this.loadData()
								uni.hideLoading()
								_this.$api.msg(res.data.data)
					        }
					    }
					   
					});
				}
			})
			
			
		},
			payOrder(orderId){
				var _this = this
				console.log(orderId)
				uni.request({
				    url:this.$url+'/order/pay/'+orderId ,//服务器端地址
				    data: {
				    },
				    method: 'POST',
				    header: {
				        'content-type': 'application/json',
						'token':token,
				    },
				    success: (res) => { 
						console.log(res.data)
				        if (res.data.code == 200) {
				       uni.requestPayment({
				           timeStamp:res.data.data.timeStamp,
				           nonceStr:res.data.data.nonceStr,
				           package: res.data.data.packageValue,
				           signType:res.data.data.signType,
				           paySign:res.data.data.paySign,
				           success(res) {
							   uni.navigateTo({
							   	url: `/pages/money/paySuccess`
							   })
				           },
				           fail(err) {
							   
				           }
				       });
						}

				    }
				   
				});
				},
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#333233';
				
				if(state==="WAITING")
				{
					stateTip = '待支付';
				}
				if(state==="PAID")
				{
					stateTip = '待发货';
				}
				if(state==="RECEIPT")
				{
					stateTip = '待收货';
				}
				if(state==="OVER")
				{
					stateTip = '已完成';
				}
			
			return {stateTip, stateTipColor};
		}
	},
}
